.bannerwrap {
	.container {
		@include media-breakpoint-down(xl) {
			max-width: 1430px;
			width: 100%; } }
	.list-banner {
		.banner-img {
			margin-top: 200px;
			@include media-breakpoint-down(lg) {
				margin-top: 162px; }
			@include media-breakpoint-down(md) {
				margin-top: 100px;
				img {
					width: 40%;
					margin: 0 auto; } }
			@include media-breakpoint-down(sm) {
				display: none; } }
		.banner-txt {
			margin-top: 100px;
			color: $white;
			@include media-breakpoint-down(md) {
				margin-top: 50px; }
			@include media-breakpoint-down(xs) {
				margin-top: 20px; }
			.small-title {
				text-transform: uppercase;
				@include media-breakpoint-down(xs) {
					display: none; } }
			.banner-title {
				@include fs(34rem);
				font-weight: bold;
				@include pos(rel,null);
				padding-bottom: 10px;
				@include media-breakpoint-down(lg) {
					@include fs(28rem); }
				@include media-breakpoint-down(md) {
					@include fs(20rem); }
				@include media-breakpoint-down(sm) {
					@include fs(18rem); }
				@include media-breakpoint-down(xs) {
					@include fs(13rem); }
				&:before {
					content: '';
					background: $white;
					width: 50px;
					height: 2px;
					@include pos(abs, null null 0 null); } }
			.banner-desc {
				margin-top: 15px;
				@include fs(18rem);
				font-weight: 300;
				@include media-breakpoint-down(md) {
					display: none; } }
			.list-service {
				width: 600px;
				margin-top: 30px;
				@include media-breakpoint-down(lg) {
					width: 100%;
					display: none; }
				ul {
					li {
						width: 25%;
						float: left;
						text-align: center;
						padding: 0 15px;
						text-transform: uppercase;
						@include fs(12rem);
						a {
							color: $main; }
						&:hover {
							a {
								letter-spacing: 0.5px; }
							img {
								@include transform(scale3d(1.1,1.1,1)); } }
						img {
							display: block;
							margin: 0 auto;
							margin-bottom: 15px;
							@include transition(all,0.3s); } } } } }
		.viewmore {
			background: $white;
			box-shadow: 0 1px 3px color-rgba($main,0.2);
			font-weight: bold;
			color: $main;
			text-transform: uppercase;
			width: 180px;
			@include bor-rad(20px);
			display: block;
			text-align: center;
			padding: 7px 0px;
			margin-top: 20px;
			@include media-breakpoint-down(sm) {
				@include fs(12rem);
				width: 100px; }
			@include media-breakpoint-down(xs) {
				@include fs(10rem);
				margin-top: 10px;
				padding: 5px 0px; }
			&:hover {
				background: $blue;
				color: $white; } }
		.note {
			color: #414042;
			font-weight: 300;
			margin-top: 15px;
			@include media-breakpoint-down(md) {
				display: none; } }
		.circle {
			float: right;
			margin-top: -40px;
			@include media-breakpoint-down(lg) {
				display: none; } } }
	.item {
		height: 640px;
		background-size: cover !important;
		@include media-breakpoint-down(lg) {
			height: 500px; }
		@include media-breakpoint-down(md) {
			height: 370px; }
		@include media-breakpoint-down(sm) {
			height: 300px; }
		@include media-breakpoint-down(xs) {
			height: 170px; }
		&.pos2 {
			.banner-img {
				margin-top: 350px;
				@include media-breakpoint-down(lg) {
					margin-top: 300px; }
				@include media-breakpoint-down(md) {
					margin-top: 100px; } }
			.banner-txt {
				.banner-title {
					color: $blue;
					&:before {
						background: $blue; } }
				.banner-desc {
					color: $white; } } }
		&.pos3 {
			.banner-txt {
				.banner-title {
					font-weight: 300;
					color: $main;
					width: 550px;
					@include media-breakpoint-down(md) {
						width: 100%; }
					&:before {
						background: none; } }
				.banner-sign {
					margin-top: 30px; } } }
		&.pos4 {
			.banner-txt {
				padding: 0 30px;
				@include media-breakpoint-down(sm) {
					padding: 0; }
				.banner-title {
					color: $main;
					width: 340px;
					@include media-breakpoint-down(lg) {
						width: 100%; }
					&:before {
						background: none; } }
				.sub-title {
					color: $main;
					@include fs(18rem);
					width: 400px;
					@include media-breakpoint-down(sm) {
						width: 100%;
						@include fs(16rem); }
					@include media-breakpoint-down(xs) {
						@include fs(13rem); } }
				.banner-desc {
					width: 435px;
					@include media-breakpoint-down(md) {
						display: none; }
					@include media-breakpoint-down(sm) {
						width: 100%; }
					@include fs(13rem); } } } }
	.slick-prev, .slick-next {
		@include pos(abs, 50% null null null);
		@include transform(translateY(-50%));
		z-index: 5;
		@include fs(0);
		color: $white;
		text-shadow: 0 1px 3px color-rgba($main,0.1);
		@include transition(all,0.3s);
		@include media-breakpoint-down(lg) {
			display: none !important; }
		&:before {
			content: '';
			font-family: 'fontawesome';
			@include fs(48rem);
			@include media-breakpoint-down(sm) {
				@include fs(18rem); } }
		// &:hover
 }		// 	color: $blue
	.slick-prev {
		left: 60px;
		&:before {
			content: '\f104'; } }
	.slick-next {
		right: 60px;
		&:before {
			content: '\f105'; } } }
.slick-dots {
	@include pos(abs, null 0 20px 0);
	text-align: center;
	@include media-breakpoint-down(sm) {
		// +pos(static,unset)
		padding: 5px 0;
		bottom: 0; }
	li {
		display: inline-block;
		width: 13px;
		height: 13px;
		@include bor-rad(50%);
		background: darken($white,10%);
		cursor: pointer;
		@include media-breakpoint-down(sm) {
			width: 8px;
			height: 8px;
			background: darken($white,5%);
			border: none; }
		+ li {
			margin-left: 10px; }
		button {
			display: none; }
		&.slick-active {
			background: $blue;
			border-color: $blue; } } }
