// CART HEADER

.cartheader {
	background: $black;
	@include pos(rel,null);
	@include media-breakpoint-down(lg) {
		display: none; }
	&:before {
		content: '';
		@include pos(abs, null 0 0 0);
		height: 3px;
		background: $blue; }
	// border-bottom: 2px solid $gray
	.sitelogo {
		float: left;
		@include media-breakpoint-down(xs) {
			padding: 15px 0;
			margin: 0; }
		a {
			@include flexbox;
			height: 60px;
			align-items: center;
			align-content: center;
			img {
				height: 31px;
				filter: invert(100%) grayscale(100%) brightness(400%); } } }
	.hotline {
		float: right;
		padding: 5px;
		// +bor-rad(50px)
		margin: 5px 0;
		// +bor-rad(50px)
		// background: color-rgba($black,0.2)
		@include media-breakpoint-down(xs) {
			display: none; }
		.fa {
			display: block;
			width: 40px;
			height: 40px;
			line-height: 40px;
			text-align: center;
			@include fs(32rem);
			@include bor-rad(50px);
			float: left;
			// background: $white
			color: $white;
			margin-right: 5px; }
		a {
			@include flexbox; }
		.phone {
			display: block;
			white-space: nowrap;
			float: left;
			line-height: 1;
			color: $white;
			padding-right: 20px;
			.label {
				@include fs(12rem);
				display: block;
				margin: 3px 0; }
			.number {
				@include fs(16rem);
				font-weight: 700;
				display: block; } } }

	.cartdes {
		float: right;
		clear: right;
		@include fs(14rem);
		color: darken($white,40%);
		@include media-breakpoint-down(xs) {
			display: none; }
		a {
			color: $white;
			&:hover {
				color: $red; } } } }

.cartfinish {
	.cartheader {
		.btn-headback {
			display: none; } } }
