.about-link {
	padding: 50px 0px;
	@include media-breakpoint-down(md) {
		padding: 30px; }
	@include media-breakpoint-down(xs) {
		padding: 15px; }
	ul {
		text-align: center;
		@include media-breakpoint-down(md) {
			display: none; }
		li {
			display: inline-block;
			@include media-breakpoint-down(md) {
				width: 100%; }
			a {
				border: 0;
				@include fs(16rem);
				text-transform: uppercase;
				@include bor-rad(0);
				font-weight: 400;
				display: flex;
				align-items: center;
				justify-content: center;
				color: #999999;
				padding: 10px 15px;
				@include pos(rel,null);
				text-transform: uppercase;
				@include media-breakpoint-down(md) {
					background: darken($white,4%);
					display: block;
					text-align: left; }
				@include media-breakpoint-down(xs) {
					@include fs(13rem); }
				&.active {
					@include bor-rad(20px);
					background: $blue;
					color: $white;
					@include media-breakpoint-down(md) {
						@include bor-rad(0);
						background: none;
						color: $blue; }
					&:hover {
						color: $white; } }
				&:hover {
					color: $blue; } } } } }
.about-murad {
