.about-history {
	background: #e7edee;
	padding: 50px 0px;
	@include media-breakpoint-down(md) {
		padding: 30px 0px; }
	@include media-breakpoint-down(xs) {
		padding: 15px 0px; }
	.desc {
		text-align: center;
		width: 80%;
		margin: 0 auto;
		margin-bottom: 30px;
		@include media-breakpoint-down(lg) {
			margin-bottom: 15px; }
		@include media-breakpoint-down(md) {
			width: 100%; }
		@include media-breakpoint-down(xs) {
			margin-bottom: 15px; } }
	.history-year {
		width: 15%;
		text-align: right;
		margin: -35px auto 0;
		margin-left: 750px;
		@include pos(rel,null);
		z-index: 5;
		@include media-breakpoint-down(lg) {
			margin: 0 auto;
			width: 30%;
			text-align: center; }
		@include media-breakpoint-down(md) {
			margin-top: -25px; }
		@include media-breakpoint-down(sm) {
			margin-top: -15px;
			width: 50%; }
		.slick-prev, .slick-next {
			width: 34px;
			height: 68px;
			@include fs(0);
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			@include transition(all,0.3s);
			color: darken($white,60%);
			&:before {
				content: '';
				font-family: 'fontawesome';
				@include fs(40rem); } }
		.slick-prev {
			&:before {
				content: '\f104'; }
			left: -30px; }
		.slick-next {
			&:before {
				content: '\f105'; }
			right: -40px; }
		.item {
			.img {
				@include media-breakpoint-down(md) {
					margin: 0 auto; } }
			.year {
				@include fs(68rem);
				font-weight: bold;
				color: $blue;
				text-align: right;
				text-shadow: -3px -3px 0 $white,3px -3px 0 $white,-3px 3px 0 $white,3px 3px 0 $white;
				@include media-breakpoint-down(lg) {
					text-align: center; }
				@include media-breakpoint-down(sm) {
					@include fs(60rem); }
				@include media-breakpoint-down(xs) {
					@include fs(48rem); } } } }
	.history-nav {
		.slick-prev, .slick-next {
			display: none !important; }
		.item {
			padding: 5px 0px;
			cursor: pointer;
			.year-nav {
				text-align: center;
				font-weight: 600;
				color: darken($white,40%);
				@include transition(all 0.4s); }
			&.slick-current {
				background: $blue;
				@include bor-rad(20px);
				.year-nav {
					color: $white; } } } } }
