
// COLOR


@import "_cartheader";
@import "_cartfooter";
@import "_cartstep";

.cartpage {

	@include media-breakpoint-down(sm) {
		padding-top: 0; }
	footer .footertop {
		display: none; }
	header {
		@include media-breakpoint-down(sm) {
			display: none; } }
	.container {
		@include media-breakpoint-down(lg) {
			width: 100%;
			max-width: 100%; } } }


// CART PAGE

.cart-title {
	font-weight: 500;
	text-transform: uppercase;
	@include fs(15rem);
	line-height: 1.5;
	color: $black;
	margin-bottom: 15px;
	@include pos(rel,null);
	padding-left: 25px;
	@include media-breakpoint-down(xs) {
		@include fs(13rem);
		padding-left: 0; }
	strong {
		color: $lred; }
	.material-icons {
		@include pos(abs, 4px null null 0);
		@include fs(18rem);
		@include media-breakpoint-down(xs) {
			display: none; } } }
.cart-page {
	padding-top: 15px;
	// +media-breakpoint-up(md)
 }	// 	padding: 0
.cart-table {
	@include bor-rad(5px);
	border: 1px solid darken($white,10%);
	width: 100%;
	@include media-breakpoint-down(lg) {
		margin-bottom: 15px; }
	th, td {
		background: none;
		color: $main;
		@include media-breakpoint-up(lg) {
			padding: 10px; } }
	th {
		display: none;
		@include media-breakpoint-up(lg) {
			display: table-cell;
			vertical-align: middle;
			font-weight: 500;
			@include fs(13rem);
			text-transform: uppercase;
			background: darken($white,5%);
			border-bottom: 1px solid darken($white,10%);
			&:last-child {
				text-align: right; } } }
	tbody {
		tr {
			@include media-breakpoint-down(md) {
				@include flexbox;
				flex-wrap: wrap;
				border-bottom: 1px solid darken($white,10%);
				&:last-child {
					border-bottom: none; } } }
		td {}
		.product {
			width: 100%;
			padding: 10px;
			@include pos(rel,null);
			@include media-breakpoint-up(lg) {
				width: calc(100% - 200px - 100px - 200px); }
			figure {
				@include flexbox;
				.product-img {
					display: block;
					width: 80px; }
				figcaption {
					width: calc(100% - 80px);
					padding-left: 10px;
					.product-name {
						@include fs(14rem);
						font-weight: 500;
						line-height: 1.5;
						margin-bottom: 5px;
						a {
							color: $black; } }
					.product-option {
						@include media-breakpoint-up(lg) {
							@include flexbox;
							flex-wrap: wrap; }
						.label {
							@include fs(13rem);
							display: inline-block;
							vertical-align: middle; }
						.product-size {
							// width: 50%
							margin-bottom: 5px;
							@include media-breakpoint-up(lg) {
								margin: 0; }
							select {
								display: inline-block;
								vertical-align: middle;
								@include bor-rad(3px);
								border: 1px solid darken($white,30%);
								color: $black;
 } }								// font-weight: 600
						.product-color {
							@include media-breakpoint-up(lg) {
								margin-left: 15px;
								@include flexbox;
								align-items: center;
								align-content: center; }
							.color-list {
								display: inline-block;
								vertical-align: top;
								@include media-breakpoint-up(lg) {
									@include flexbox;
									align-content: center;
									align-items: center; } }
							a {
								display: inline-block;
								border: 1px solid darken($white,10%);
								padding: 2px;
								&.active {
									border-color: $blue; }
								+ a {
									margin-left: 3px; } }
							.color-img {
								width: 14px;
								height: 14px; }
 } }							// width: 50%
					.removeproduct {
						color: $lred;
						@include fs(13rem);
						@include media-breakpoint-up(lg) {
							@include pos(static,unset);
							margin-top: 10px;
							display: block;
							color: darken($white,20%); }
						&:hover {
							color: $red; }
						.material-icons {
							@include fs(16rem);
							line-height: 1;
							@include media-breakpoint-down(xs) {
								display: none; } } } } } }
		.price {
			margin-left: 100px;
			width: calc(100% - 200px);
			@include media-breakpoint-up(lg) {
				width: 150px;
				margin-left: 0; }
			.new {
				display: block;
				@include fs(15rem);
				font-weight: 600;
				color: $blue;
				line-height: 1;
				@include media-breakpoint-up(lg) {
					color: $black;
					font-weight: 500; } }
			.old {
				display: inline-block;
				@include fs(11rem);
				text-decoration: line-through;
				color: darken($white,40%);
				font-weight: 500;
				line-height: 1;
				@include media-breakpoint-up(lg) {
					font-weight: 400;
					color: darken($white,20%); } }
			.sale {
				display: inline-block;
				@include fs(12rem);
				line-height: 1;
				color: $lred; } }
		.amount {
			margin-bottom: 10px;
			@include media-breakpoint-up(lg) {
				margin-bottom: 0;
				width: 100px; }
			.input-number {
				width: 90px;
				background: darken($white,5%);
				border: none;
				overflow: hidden;
				display: flex;
				.btn-spin {
					width: 30px;
					height: 30px;
					line-height: 30px;
					background: darken($white,10%);
					text-align: center; }
				input {
					line-height: 24px;
					width: 30px;
					text-align: center; } } }
		.total {
			display: none;
			@include fs(15rem);
			font-weight: 600;
			color: $blue;
			line-height: 1;
			@include media-breakpoint-up(lg) {
				display: table-cell;
				width: 150px;
				text-align: right; } } } }
.cart-btnwrap {
	@include pos(fix, null 0 0 0);
	z-index: 50;
	padding: 10px 15px;
	background: $white;
	box-shadow: 0 -3px 20px -10px color-rgba($black,0.5);
	border-top: 1px solid darken($white,5%);
	@include media-breakpoint-up(xl) {
		@include pos(static,unset);
		box-shadow: none;
		border-top: none;
		padding: 10px 0;
		margin-bottom: 20px; }
	.total-amount {
		@include flexbox;
		margin-bottom: 10px;
		@include media-breakpoint-up(md) {
			justify-content: flex-end;
			clear: both; }
		.label {
			@include fs(14rem);
			font-weight: 500;
			width: 100px;
			color: $main;
			@include media-breakpoint-up(md) {
				width: unset;
				margin-top: 3px; } }
		.value {
			width: calc(100% - 100px);
			text-align: right;
			@include fs(18rem);
			font-weight: 600;
			color: $lred;
			@include media-breakpoint-up(md) {
				width: unset; }
			.des {
				display: block;
				@include fs(12rem);
				color: $main; } } }
	.btn-backstep {
		display: block;
		color: darken($white,40%);
		line-height: 40px;
		float: left;
		&:before {
			content: '\f104';
			margin-right: 10px;
			font-family: 'fontawesome'; }
		&:hover {
			color: $blue; }
		@include media-breakpoint-down(sm) {
			display: none; } }
	.btn-nextstep {
		display: block;
		@include media-breakpoint-up(md) {
			float: right;
			padding: 0px 20px; } }
	.cart-btnwrap,.btn-nextstep {
		display: block;
		line-height: 40px;
		border-radius: 50px;
		text-align: center;
		color: $white;
		background: $blue;
		box-shadow: 0px 10px 20px -10px $blue; } }

.product-lastview {
	margin-bottom: 40px;
	.carttitle {
		@include media-breakpoint-down(xs) {
			margin-bottom: 15px; }
		&:before {
			content: '\f017'; } }
	.cart-lastview-slide {
		.slick-prev, .slick-next {
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			@include fs(0);
			width: 36px;
			height: 36px;
			line-height: 36px;
			text-align: center;
			@include bor-rad(50px);
			background: darken($white,10%);
			z-index: 5;
			@include media-breakpoint-down(xs) {
				display: none !important; }
			&.slick-disabled {
				opacity: 0; }
			&:before {
				content: '';
				font-family: 'fontawesome';
				@include fs(18rem); } }
		.slick-prev {
			left: 0;
			&:before {
				content: '\f104'; } }
		.slick-next {
			right: 0;
			&:before {
				content: '\f105'; } }
		.item {
			padding: 0px 10px; }
		.productcol {

			figure {
				.productimg {
					img {
						margin: 0 auto;
						width: 50%;
						padding: 15px; } } } } } }
.cart-page {
	.btn-wrap {
		// margin-bottom: 30px
		@include media-breakpoint-down(md) {
			margin-bottom: 20px; }
		@include media-breakpoint-down(xs) {
			padding: 0px 15px 10px;
			// +flexbox
			// text-align: center
			// +pos(fix, null 0 0 0)
			// z-index: 50
			// border-top: 1px solid darken($white,10%)
 } } }   //	// box-shadow: 0 0 30px color-rgba($black,0.1)
.btn-back {
	display: block;
	background: darken($white,10%);
	color: $main;
	// +fs(14rem)
	@include bor-rad(5px);
	line-height: 40px;
	padding: 0px 20px;
	float: left;
	text-align: center;
	@include media-breakpoint-down(xs) {
		background: none;
		width: 100%;
		margin-bottom: 0;
		color: darken($white,60%); }
	&:hover {
		background: darken($white,40%);
		color: $white; }
	&:before {
		content: '\f104';
		font-family: 'fontawesome';
		margin-right: 15px;
		@include media-breakpoint-down(sm) {
			display: none; } } }
.btn-continue {
	display: block;
	background: $blue;
	overflow: hidden;
	color: $white;
	line-height: 40px;
	padding: 0px 20px;
	float: right;
	margin: 0;
	@include bor-rad(5px);
	text-align: center;
	@include media-breakpoint-down(xs) {
		width: 100%;
		margin: 0;
		text-transform: uppercase;
		@include fs(15rem); }
	&:hover {
		color: $white; }
	&:after {
		content: '\f105';
		font-family: 'fontawesome';
		margin-left: 15px;
		@include media-breakpoint-down(sm) {
			display: none; } }
	&:before {
		background: color-rgba($white,0.2); } }

.cartpage {
	.nopadding {
		background: $white;
		+ .nopadding {
			border-left: 1px solid darken($white,10%); } } }

.select-group {
	margin-bottom: 10px;
	input[type="radio"] {
		display: none;
		+ label {
			padding: 5px 0 5px 20px;
			@include pos(rel,null);
			color: darken($white,60%);
			@include transition(all,0.3s);
			@include fs(14rem);
			@include media-breakpoint-down(xs) {
				padding: 0px 0 0 20px; }
			&:hover {
				color: $red; }
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 14px;
				@include bor-rad(50px);
				@include pos(abs, 7px null null 0);
				border: 1px solid darken($white,40%);
				@include media-breakpoint-down(xs) {
					top: 3px; } }
			&:after {
				content: '';
				display: block;
				width: 14px;
				height: 14px;
				border: 4px solid $blue;
				@include pos(abs, 7px null null 0);
				@include bor-rad(50px);
				opacity: 0;
				@include transition(all,0.3s);
				@include media-breakpoint-down(xs) {
					top: 3px; } } }
		&:checked {
			+ label {
				color: $main;
				&:after {
					opacity: 1; } } } } }

.check-group {
	margin-bottom: 10px;
	input {
		display: none;
		+ label {
			color: $main;
			padding-left: 25px;
			@include pos(rel,null);
			&:hover {
				color: $black; }
			&:before {
				content: '';
				display: block;
				width: 16px;
				height: 16px;
				// +bor-rad(3px)
				border: 1px solid darken($white,20%);
				@include pos(abs, 1px null null 0); }
			&:after {
				content: '\f00c';
				font-family: 'fontawesome';
				display: block;
				width: 16px;
				height: 16px;
				line-height: 14px;
				text-align: center;
				// +bor-rad(3px)
				color: $blue;
				border: 1px solid darken($white,20%);
				@include pos(abs, 1px null null 0);
				z-index: 1;
				@include transition(all,0.5s);
				opacity: 0; } }
		&:checked {
			+ label {
				&:after {
					opacity: 1; } } } } }

