.cart-userinfo {
	padding: 15px 0;
	@include media-breakpoint-up(md) {
		padding: 0; }
	@include media-breakpoint-down(xs) {
		padding: 0; } }
.cart-form {
	.form-wrap {
		@include bor-rad(5px);
		border: 1px solid darken($white,10%);
		padding: 10px 15px;
		margin: 0; }
	.form-group {
		@include flexbox;
		flex-wrap: wrap;
		margin-bottom: 10px;
		@include media-breakpoint-up(md) {
			margin-bottom: 15px; }
		.label {
			width: 200px;
			color: $main;
			@include media-breakpoint-down(sm) {
				width: 150px; }
			@include media-breakpoint-down(xs) {
				width: 100%;
				margin-bottom: 5px; }
			@include media-breakpoint-up(md) {
				@include flexbox;
				padding: 10px 0; }
			label {
				@include fs(14rem);
				font-weight: 500;
				@include media-breakpoint-up(md) {
 } } }   					// +fs(13rem)
		.input {
			width: calc(100% - 200px);
			@include media-breakpoint-down(sm) {
				width: calc(100% - 150px); }
			@include media-breakpoint-down(xs) {
				width: 100%; } }
		input, textarea, select {
			width: 100%;
			border: none;
			border-bottom: 2px solid darken($white,20%);
			padding: 10px 0px;
			@include fs(14rem);
			@include bor-rad(0px);
			@include transition(all,0.3s);
