.solution-top {
	background-size: cover;
	margin-top: 50px;
	padding: 50px 0px;
	@include media-breakpoint-down(md) {
		padding: 30px 0px;
		margin-top: 30px; }
	@include media-breakpoint-down(xs) {
		padding: 15px 0px;
		margin-top: 15px; }
	.box-solution {
		width: 60%;
		float: right;
		padding-right: 80px;
		@include media-breakpoint-down(md) {
			padding-right: 30px;
			width: 75%; }
		@include media-breakpoint-down(sm) {
			width: 100%; }
		@include media-breakpoint-down(xs) {
			padding: 0 15px; }
		.pagetitle {
			color: $white;
			@include fs(20rem);
			@include media-breakpoint-down(xs) {
				@include fs(16rem); }
			&:before {
				background: $white; } }
		.solution-desc {
			color: $white;
			margin-top: 10px;
			text-align: center;
			@include media-breakpoint-down(md) {
				@include fs(13rem); }
			@include media-breakpoint-down(xs) {
				display: none; } } } }
.solution-step {
	text-align: center;
	padding: 50px 0px;
	margin: 0 50px;
	@include media-breakpoint-down(lg) {
		margin: 0; }
	@include media-breakpoint-down(md) {
		padding: 30px 0px; }
	@include media-breakpoint-down(xs) {
		padding: 15px 0px; }
	.stepcol {
		border: 2px solid darken($white,16%);
		padding: 50px;
		width: 280px;
		margin: 0 auto;
		@include transition(all 0.5s);
		cursor: pointer;
		@include pos(rel,null);
		@include media-breakpoint-down(md) {
			width: 100%;
			padding: 15px; }
		&:before {
			content: '\f0d7';
			font-family: 'fontawesome';
			@include pos(abs, null null -20px);
			@include fs(20rem);
			color: $blue;
			opacity: 0;
			@include transition(all 0.5s); }
		figure {
			figcaption {
				@include pos(rel,null);
				.stepnumber {
					@include fs(50rem);
					font-weight: 700;
					color: darken($white,4%);
					@include media-breakpoint-down(md) {
						@include fs(30rem); } }
				.stepname {
					color: $blue;
					@include pos(abs, null null 15px 50%);
					@include transform(translateX(-50%));
					width: 100%;
					@include media-breakpoint-down(md) {
						@include pos(static,unset);
						@include transform(none); } } } }
		&:hover {
			border: 2px solid $blue;
			@include header-sd();
			&:before {
				opacity: 1; } }
		&.active {
			border: 2px solid $blue;
			@include header-sd();
			&:before {
				opacity: 1; } } } }
.solution-product {
	padding-bottom: 50px;
	@include media-breakpoint-down(md) {
		padding-bottom: 30px; }
	@include media-breakpoint-down(xs) {
		padding-bottom: 15px; } }
