// USER CONTROL PANEL
.userprofilepage {
	.header-top {
		@include media-breakpoint-down(lg) {
			display: none; } }
	.btn-showmenu {
		@include media-breakpoint-down(lg) {
			display: none; } } }
.signup-page {}

.signup-panel {
	padding: 20px; }
.signup-panel, .login-panel {
	padding: 20px;
	margin-top: 30px;
	border: 1px solid darken($white ,10%);
	margin-bottom: 30px;
	@include media-breakpoint-down(sm) {
		margin-bottom: 15px; } }
.signup-form, .login-form {
	width: 100%;
	// margin-bottom: 40px
	.headtitle {
		@include fs(24rem);
		text-align: center;
		// margin-bottom: 20px
		color: $blue;
		background: none;
		margin-bottom: 0;
		&:after {
			content: '';
			display: block;
			width: 50px;
			height: 2px;
			background: $blue;
			margin: 10px auto 0; }
		&:before {
			display: none; } }
	.panel-heading {
		margin-bottom: 10px;
		color: darken($white,60%); }
	.form-group {
		margin-bottom: 10px;
		label {
			@include fs(14rem);
			margin-bottom: 5px; }
		input, select {
			width: 100%;
			@include bor-rad(0px);
			font-size: 0.9rem;
			border: 1px solid darken($white,15%);
			padding: 0px 15px;
			line-height: 38px;
			&:hover, &:active, &:focus {
				border-color: darken($white,30%); } }
		select {
			padding: 10px 15px; }
		.fa.fa-exclamation {
			top: 42px;
			right: 10px; } }
	input[type="submit"] {
		line-height: 40px;
		padding: 0px 20px;
		@include bor-rad(0);
		background: $blue;
		color: $white;
		font-weight: 300 !important;
		@include fs(14rem);
		border: none;
		text-transform: uppercase;
		&:hover {
			background: lighten($blue,10%); } } }
html body .RadInput_Default .riTextBox {
	width: 100% !important;
	@include bor-rad(3px);
	border: 1px solid darken($white,15%) !important;
	// padding: 10px 15px !important
	padding: 0px 15px !important;
	line-height: 40px !important;
	font-size: 0.9rem !important; }

.login-form {
	.form-group {
		width: 100%;
		@include pos(rel,null);
		label {
			width: 40px;
			line-height: 40px;
			text-align: center;
			@include fs(18rem);
			color: $blue;
			@include pos(abs, 0 null null 0);
			.fa {
				&:before {
					line-height: 1; } } }
		input {
			padding-left: 40px; }
		a {
			color: darken($white,60%);
			margin-right: 10px;
			&:hover {
				color: $blue; } }
		&.forget {
			@extend .check-group;
			label {
				width: auto;
				line-height: 1.5;
				@include fs(14rem);
				@include pos(static,null);
				color: darken($white,60%);
				&:hover {
					color: $blue; } } } }

	input[type="submit"] {
		width: 100%;
		padding: 0px 20px;
		display: inline-block;
		border: none;
		width: 150px; }
	.btn-login {
		text-align: center;
		background: none;
		&:before {
			display: none; } } }

// USER SIDEBAR

.user-sidebar {
	margin: 150px 0 20px;
	@include pos(rel,null);
	@include media-breakpoint-down(md) {
		&:after {
			content: '\f107';
			font-family: 'fontawesome';
			@include pos(abs, 50% 15px null null);
			@include transform(translateY(-50%));
			color: $blue;
			@include fs(24rem); } }
	.userinfo {
		// background: $blue
		border: 1px dashed $blue;
		background: lighten($blue,40%);
		@include bor-rad(3px);
		// border-left: 5px solid $red
		figure {
			&:after {
				content: '';
				display: table;
				width: 100%;
				clear: both; }
			.user-img {
				display: none;
				width: 80px;
				float: left;
				@include media-breakpoint-down(lg) {
					width: 60px; } }
			figcaption {
				padding: 10px 15px;
				@include media-breakpoint-down(lg) {
					padding: 7px 15px; }
				.title {
					color: $main;
					opacity: 0.8;
					font-weight: 400;
					@include fs(13rem); }
				.user-name {
					text-transform: uppercase;
					font-weight: 700;
					@include fs(16rem);
					color: $black; } } } } }
.user-sidelink {
	background: darken($white,0%);
	border: 1px solid darken($white,20%);
	@include bor-rad(3px);
	margin-bottom: 60px;
	@include media-breakpoint-down(md) {
		margin-bottom: 30px; }
	ul {
		padding: 10px 0;
		li {
			width: 100%;
			&:before {
				background: lighten($blue,40%); }
			a {
				display: block;
				color: $main;
				padding: 0px 20px;
				line-height: 40px;
				text-transform: uppercase;
				@include fs(13rem);
				font-weight: 600;
				&:hover {
					color: $blue; } }
			&.active {
				a {
					color: $blue;
					background: lighten($blue,40%); } } } } }

