.account-form {
	width: 50%;
	margin: 0 auto;
	@include media-breakpoint-down(lg) {
		width: 60%; }
	@include media-breakpoint-down(md) {
		width: 70%; }
	@include media-breakpoint-down(xs) {
		width: 100%; }
	.form-group {
		margin-bottom: 15px;
		label {
			text-transform: uppercase;
			@include fs(13rem);
			color: darken($white,60%);
			margin-bottom: 5px; }
		input, select {
			width: 100%;
			border: 1px solid darken($white,15%);
			padding: 10px 15px;
			font-size: 0.875rem;
			-webkit-transition: all, 0.3s;
			@include transition(all, 0.3s);
			@include bor-rad(3px);
			margin-bottom: 15px;
			&:hover, &:active, &:focus {
				border-color: darken($white,40%); } } }
	.gender-select {
		ul {
			padding: 10px 0 0;
			&:after {
				display: table;
				content: '';
				width: 100%;
				clear: both; }
			li {
				width: 50%;
				float: left; } }
		input[type="radio"] {
			display: none;
			+ label {
				font-weight: 600;
				color: $main;
				padding-left: 30px;
				text-transform: none;
				@include fs(15rem);
				@include pos(rel,null);
				@include transition(all,0.5s);
				&:hover {
					color: $black; }
				&:before {
					content: '';
					display: block;
					width: 16px;
					height: 16px;
					border: 1px solid darken($white,40%);
					opacity: 0.5;
					@include bor-rad(50%);
					@include pos(abs, 2px null null 0);
					@include transition(all,0.5s); }
				&:after {
					content: '';
					display: block;
					width: 14px;
					height: 14px;
					border: 3px solid $white;
					background: $blue;
					opacity: 0;
					@include bor-rad(50%);
					@include pos(abs, 3px null null 1px);
					@include transition(all,0.5s); } }
			&:checked {
				+ label {
					&:before, &:after {
						opacity: 1; } } } } }
	.birthday {
		ul {
			margin: 0 -10px;
			li {
				width: 33.33%;
				float: left;
				padding: 0 10px;
				@include media-breakpoint-down(xs) {
					select {
						@include fs(14rem);
						padding: 10px 10px; } } } } }
	.check-group {
		input[type='checkbox'] {
			display: none;
			+ label {
				color: darken($white,60%);
				padding-left: 30px;
				@include fs(14rem);
				@include pos(rel,null);
				@include transition(all,0.5s);
				&:hover {
					color: $black; }
				&:before {
					content: '';
					display: block;
					width: 16px;
					height: 16px;
					border: 1px solid darken($white,40%);
					opacity: 0.5;
					@include pos(abs, 1px null null 0);
					@include transition(all,0.5s); }
				&:after {
					content: '\f00c';
					font-family: 'fontawesome';
					display: block;
					line-height: 14px;
					@include fs(16rem);
					color: $blue;
					opacity: 0;
					@include pos(abs, 2px null null 1px);
					@include transition(all,0.5s); } }
			&:checked {
				+ label {
					&:before, &:after {
						opacity: 1; } } } } }
	.check-group {
		margin-bottom: 15px; }
	.changepw-form {
		display: none; }
	input[type='submit'] {
		color: $white;
		background: $blue;
		padding: 0 20px;
		border: none;
		@include fs(14rem);
		line-height: 36px;
		text-transform: uppercase;
		margin: 5px 0 40px;
		@include transition(all,0.5s);
		@include bor-rad(3px);
		&:hover {
			background: lighten($blue,10%); } } }

