.fancybox-overlay {
	background: color-rgba($black,0.7); }
.fancybox-close {
	background: $red;
	@include bor-rad(50%);
	color: color-rgba($white,0.8);
	@include fs(36rem);
	font-weight: lighter;
	line-height: 36px;
	text-align: center;
	@include transform(rotateZ(45deg));
	@include transition(all,0.3s);
	&:before {
		content: "+"; }
	&:hover {
		color: $white;
		text-shadow: 0 0 10px $white; } }
.fancybox-nav {
	span {
		display: block;
		width: 40px;
		height: 40px;
		@include fs(28rem);
		line-height: 32px;
		text-align: center;
		color: $white;
		border: 2px solid $white;
		@include bor-rad(50%);
		visibility: visible;
		opacity: 0.5;
		@include transition(all,0.3s);
		&:hover {
			opacity: 1;
			background: $white;
			color: $red; } }
	&.fancybox-prev {
		span {
			&:before {
				content: "\f104";
				font-family: 'fontawesome'; } } }
	&.fancybox-next {
		span {
			&:before {
				content: "\f105";
				font-family: 'fontawesome'; } } } }


.RadCaptcha {
	#ctl00_altContent2_ctl01_captcha_ctl01 {
		float: left;
		+ p {
			float: left;
			margin-top: 0; } }
	span {
		@include fs(13rem);
		color: $red;
		margin-bottom: 5px; }
	p {
		margin-top: 0.875rem; }
	img {
		display: inline-block!important;
		margin-right: 0.9375rem;
		vertical-align: middle; }
	input {
		width: 250px;
		@include media-breakpoint-down(xs) {
			width: 100%; } }
	label {
		display: none; }
	.rcRefreshImage {
		display: inline-block!important;
		vertical-align: middle;
		position: relative;
		color: transparent;
		width: 30px;
		height: 30px;
		&:after {
			content: '\f021';
			font-family: "FontAwesome";
			font-size: 18px;
			color: $black;
			position: absolute;
			top: 50%;
			right: 0;
			left: 0;
			@include transform(translate(0, -50%)); } }
	#ctl00_mainContent_ctl03_captcha_ctl01 {
		float: left;
		+ p {
			display: table-cell;
			padding: 7px 0;
			input {
				width: 100%; } } } }

