.job-wrap {
	.table-responsive {
		overflow-y: hidden; }
	table {
		tr {
			th, td {
				border-color: darken($white, 15%);
				padding: 15px; }
			th {
				color: $main;
				border-width: 1px;
				white-space: nowrap; }
			td {
				border-top: 1px dotted darken($white, 15%);
				padding: 15px 10px;
				vertical-align: top;
				h2 {
					margin: 0 0 10px;
					line-height: 1.3em;
					font-weight: bold;
					cursor: pointer;
					span {
						display: inline-block; }

					&:hover {
						color: $main;
						text-decoration: underline; } }
				.job-content {
					display: none; }
				&.white-space {
					white-space: nowrap; }
				&.show {
					h2 {
						color: $main;
						span {
							border-bottom: 1px solid $main; } } } } } } }
.button-page {}
.career-frame {
	display: none;
	margin-top: 20px; }
.apply-frm {
	overflow: hidden;
	padding: 20px 0;
	h4 {
		color: $main;
		font-size: 14px;
		font-weight: normal; }
	.form-group {
