.product-page {
	padding: 50px 0px 0px;
	@include media-breakpoint-down(md) {
		padding: 30px 0px 0; }
	@include media-breakpoint-down(xs) {
		padding: 15px 0px 0; }
	.product-lastview {
		padding-top: 50px;
		@include media-breakpoint-down(md) {
			padding-top: 30px; }
		@include media-breakpoint-down(xs) {
			margin-bottom: 0;
			padding-top: 15px; } } }
.list-product {
	margin-top: 50px;
	@include media-breakpoint-down(md) {
		margin-top: 30px; }
	@include media-breakpoint-down(xs) {
		margin-top: 15px; }
	.item {
		@include media-breakpoint-down(xs) {
			padding: 0 5px; } }
	.col-lg-4 {
		height: auto;
		@include media-breakpoint-up(lg) {
			padding: 0 10px; }
		@include media-breakpoint-down(md) {
			margin-bottom: 200px;
			&:last-child {
				margin: 0; } } }
	.slick-dots {
		@include media-breakpoint-down(lg) {
			display: none !important; } }
	.slick-prev, .slick-next {
		@include pos(abs, 50% null null null);
		@include transform(translateY(-50%));
		z-index: 5;
		@include fs(0);
		color: $main;
		text-shadow: 0 1px 3px color-rgba($main,0.1);
		@include transition(all,0.3s);
		@include media-breakpoint-down(lg) {
			display: none !important; }
		&:before {
			content: '';
			font-family: 'fontawesome';
			@include fs(40rem);
			width: 25px;
			height: 25px;
			@include transition(all,0.3s);
			opacity: 0;
			@include media-breakpoint-down(sm) {
				@include fs(18rem); } }
		&:hover {
			background: $blue;
			color: $white;
			&:before {
				padding: 0 15px; } } }
	.slick-prev {
		left: -30px;
		&:before {
			content: '\f104'; } }
	.slick-next {
		right: -30px;
		&:before {
			content: '\f105'; } }
	&:hover {
		.slick-prev, .slick-next {
			&:before {
				opacity: 1; } } } }
.product-tabs {
	margin-top: 50px;
	@include media-breakpoint-down(md) {
		margin-top: 30px; }
	.nav-tabs {
		border: 0;
		border-top: 1px solid darken($white,10%);
		border-bottom: 1px solid darken($white,10%);
		padding: 5px 0px;
		text-align: center;
		@include media-breakpoint-down(lg) {
			display: none; }
		.nav-item {
			margin: 0;
			margin-left: 15px;
			float: none;
			display: inline-block;
			@include media-breakpoint-down(lg) {
				display: block;
				width: 100%;
				margin: 0; }
			&:last-child {
				margin: 0;
				.nav-link {
					&:before {
						background: none; } } }
			.nav-link {
				border: 0;
				@include fs(16rem);
				text-transform: uppercase;
				@include bor-rad(0);
				font-weight: 400;
				display: flex;
				align-items: center;
				justify-content: center;
				color: darken($white,40%);
				padding: 10px 15px;
				@include pos(rel,null);
				@include media-breakpoint-down(lg) {
					padding: 10px;
					@include fs(14rem);
					background: darken($white,4%); }
				&:before {
					content: '';
					background: darken($white,40%);
					width: 1px;
					height: 20px;
					@include pos(abs, 50% 0 null null);
					@include transform(translateY(-50%));
					@include media-breakpoint-down(lg) {
						display: none; } }
				&.active {
					border: 0;
					@include bor-rad(20px);
					background: $blue;
					color: $white;
					@include media-breakpoint-down(lg) {
						@include bor-rad(0px); }
					&:first-child {
						&:before {
							background: none; } }
					&:hover {
						color: $white; } }
				&:hover {
					color: $blue; } } } } }
.productcol {
	height: 100%;
	@include pos(rel,null);
	&.green {
		.btn-buy {
			a {
				background: $green; } } }
	&.pink {
		.btn-buy {
			a {
				background: $pink; } } }
	&.purple {
		.btn-buy {
			a {
				background: $purple; } } }
	&.blue {
		.btn-buy {
			a {
				background: $blue; } } }
	&.brown {
		.btn-buy {
			a {
				background: $brown; } } }
	figure {
		.productimg {
			display: block;
			text-align: center;
			overflow: hidden;
			img {
				@include transition(all,0.4s);
				margin: 0 auto;
				@include media-breakpoint-down(xs) {
					width: 80%; } } }
		figcaption {
			text-align: center;
			padding: 30px 24px 80px;
			@include media-breakpoint-down(lg) {
				padding: 30px 10px 60px; }
			@include media-breakpoint-down(xs) {
				padding: 15px 0px 50px; }
			.productname {
				display: -webkit-box;
				display: -ms-flexbox;
				display: -webkit-flex;
				display: flex;
				-webkit-justify-content: left;
				-webkit-box-pack: left;
				-ms-flex-pack: left;
				justify-content: left;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-box-orient: vertical;
				overflow: hidden;
				text-overflow: ellipsis;
				-webkit-line-clamp: 2;
				line-height: 1.5;
				min-height: 44px;
				a {
					color: $main;
					text-transform: uppercase;
					line-height: 1.3rem;
					font-weight: bold;
					@include fs(13rem);
					@include media-breakpoint-down(xs) {
						@include fs(12rem); } } }
			.productdesc {
				color: $main;
				@include fs(13rem);
				margin-bottom: 10px;
				font-weight: 300;
				font-style: italic;
				margin-top: 5px;
				@include pos(rel,null);
				display: -webkit-box;
				display: -ms-flexbox;
				display: -webkit-flex;
				display: flex;
				min-height: 48px;
				-webkit-justify-content: left;
				-webkit-box-pack: left;
				-ms-flex-pack: left;
				justify-content: left;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-box-orient: vertical;
				overflow: hidden;
				text-overflow: ellipsis;
				-webkit-line-clamp: 2;
				@include media-breakpoint-down(md) {
					min-height: 40px; }
				&:before {
					content: '';
					background: $main;
					width: 50px;
					height: 1px;
					@include pos(abs, null null 0 50%);
					@include transform(translateX(-50%));
					@include media-breakpoint-down(md) {
						display: none; } } }
			.productprice {
				color: $main;
				font-weight: bold;
				@include fs(17rem);
				@include media-breakpoint-down(xs) {
					@include fs(13rem); } }
			.favorite {
				margin-top: 5px;
				@include fs(13rem);
				li {
					display: inline-block;
					vertical-align: middle;
					a {
						color: darken($white,40%);
						&:before {
							content: '\f006';
							font-family: 'fontawesome'; }
						&:hover {
							color: $yellow; } }
					&.active {
						a {
							color: $yellow;
							&:before {
								content: '\f005'; } } } } } }
		&:hover {
			.productimg {
				img {
					@include transform(scale3d(1.1,1.1,1)); } }
			figcaption {
				.productname {
					a {
						color: $blue; } } } }
		.btn-buy {
			@include pos(abs, null null 15px 50%);
			@include transform(translateX(-50%));
			@include media-breakpoint-down(lg) {
				bottom: 0; }
			@include media-breakpoint-down(xs) {
				width: 100%;
				bottom: 5px; }
			a {
				text-align: center;
				display: block;
				text-transform: uppercase;
				padding: 10px 0px;
				text-align: center;
				width: 180px;
				margin: 0 auto;
				background: $orange;
				@include bor-rad(30px);
				font-weight: 500;
				color: $white;
				margin-top: 20px;
				overflow: hidden;
				@include media-breakpoint-down(xs) {
					width: 100%;
					@include fs(11rem);
					padding: 7px 0px; }
				&:before {
					background: color-rgba($white,0.2); }
				span {
					padding-right: 20px;
					@include media-breakpoint-down(xs) {
						padding-right: 10px; } } } } } }
