.slidewrap {
	@include pos(rel,null);
	@include flexbox;
	margin-bottom: 30px;
	@include media-breakpoint-down(xs) {
		margin-bottom: 20px; }
	.product-name {
		@include fs(15rem);
		font-weight: 700;
		color: $blue;
		background: lighten($blue,65%);
		padding: 10px;
		margin: 10px 0;
		@include bor-rad(5px);
		width: 100%; }
	.promotion {
		color: $white;
		@include bor-rad(3px);
		@include fs(11rem);
		font-weight: 700;
		text-transform: uppercase;
		line-height: 1;
		padding: 3px 5px;
		margin-bottom: -17px;
		@include pos(rel,null);
		z-index: 5;
		&.new {
			background: $blue; }
		&.sale {
			background: $blue; } }
	.product-slide {
		width: calc(100% - 100px);
		margin-left: 100px;
		@include media-breakpoint-down(md) {
			width: calc(100% - 64px);
			margin-left: 64px; }
		@include media-breakpoint-down(xs) {
			width: 100%;
			margin-left: 0;
			margin-bottom: 15px; }
		.item {
			.fancybox {
				@include flexbox;
				width: 100%;
				height: 480px;
				justify-content: center;
				align-items: center;
				@include media-breakpoint-down(md) {
					height: 350px; }
				@include media-breakpoint-down(xs) {
					height: 200px; }
				img {
					max-width: 100%;
					max-height: 100%;
					@include media-breakpoint-down(md) {
						max-width: 100%;
						max-height: 100%; } } } } }
	.product-nav {
		@include pos(abs, 0 null null 0);
		width: 84px;
		@include media-breakpoint-down(md) {
			width: 64px; }
		@include media-breakpoint-down(xs) {
			@include pos(rel,null);
			width: 100%; }
		.slick-prev, .slick-next {
			@include fs(0);
			display: block;
			width: 100%;
			text-align: center;
			line-height: 1;
			color: darken($white,40%);
			@include media-breakpoint-down(xs) {
				@include pos(abs, 50% null null null);
				@include transform(translateY(-50%));
				z-index: 5;
				width: unset; }
			&:hover {
				color: $main; }
			&:before {
				content: '';
				font-family: 'fontawesome';
				@include fs(28rem); } }
		.slick-prev {
			margin-bottom: 30px;
			@include media-breakpoint-down(md) {
				margin-bottom: 0; }
			@include media-breakpoint-down(xs) {
				left: 0; }
			&:before {
				content: '\f106';
				@include media-breakpoint-down(xs) {
					content: '\f104'; } } }
		.slick-next {
			margin-top: 30px;
			@include media-breakpoint-down(md) {
				margin-top: 0; }
			@include media-breakpoint-down(xs) {
				right: 0; }
			&:before {
				content: '\f107';
				@include media-breakpoint-down(xs) {
					content: '\f105'; } } }
		.item {
			padding: 5px 0;
			cursor: pointer;
			border: 1px solid darken($white,10%);
			margin-bottom: 10px;
			@include media-breakpoint-down(xs) {
				padding: 0 5px;
				width: 60% !important;
				margin-bottom: 0;
				margin-right: 10px; }
			&.slick-current {
				@include pos(rel,null);
				&:before {
					content: '';
					background: color-rgba($black,0.5);
					@include pos(abs, 0 0 0 0);
					width: 100%;
					height: 100%; } }
			.img {
				width: 80px;
				height: 80px;
				@include flexbox;
				justify-content: center;
				align-items: center;
				border: 1px solid darken($white,20%);
				cursor: pointer;
				overflow: hidden;
				@include transition(all,0.5s);
				@include media-breakpoint-down(md) {
					width: 60px;
					height: 60px; }
				@include media-breakpoint-down(xs) {
					width: 100%;
					height: 70px; }
				img {
					max-width: 45%;
					width: 100%;
					@include media-breakpoint-down(xs) {
						max-width: 30%; } } }
			&.slick-current {
				.img {
					border: 1px solid $blue; } } } } }
.product-info {
	.product-name {
		@include fs(24rem);
		text-transform: uppercase;
		@include media-breakpoint-down(md) {
			@include fs(20rem); }
		@include media-breakpoint-down(xs) {
			@include fs(16rem);
			line-height: 1.5;
			font-weight: 600; } }
	.smalltitle {
		color: darken($white,40%);
		font-style: italic;
		padding: 5px 0px; }
	.list-start {
		.fa {
			color: #ffcf00; } }
	.product-overview {
		.group {
			padding: 10px 0px;
			border-bottom: 1px solid darken($white,8%);
			span {
				width: 200px;
				float: left;
				display: block;
				font-weight: bold;
				@include fs(14rem);
				padding-right: 20px;
				@include media-breakpoint-down(xs) {
					float: none;
					width: 100%;
					margin-bottom: 5px; } }
			.value {
				float: left;
				width: calc(100% - 200px);
				@include fs(14rem);
				@include media-breakpoint-down(xs) {
					float: none;
					width: 100%; }
				&.price {
					@include fs(18rem);
					color: $blue;
					font-weight: 600; }
				p {
					margin-bottom: 15px;
					@include media-breakpoint-down(xs) {
						margin-bottom: 0; } } } } }
	.buynow {
		background: $blue;
		width: 220px;
		display: block;
		padding: 10px 0;
		text-align: center;
		color: $white;
		font-weight: bold;
		text-transform: uppercase;
		@include bor-rad(20px);
		margin-top: 15px;
		@include media-breakpoint-down(xs) {
			@include pos(fix, null null 0 0);
			width: 100%;
			@include bor-rad(0);
			z-index: 9; }
		span {
			padding-right: 10px; }
		&:before {
			background: color-rgba($white,0.2); } } }
.product-description {
	padding-top: 50px;
	@include media-breakpoint-down(md) {
		padding-top: 30px; }
	@include media-breakpoint-down(sm) {
		padding-top: 15px; }
	.nav-tabs {
		border: 0;
		border-bottom: 2px solid $blue;
		@include media-breakpoint-down(md) {
			display: none; }
		.nav-item {
			margin: 0;
			margin-right: 10px;
			@include media-breakpoint-down(md) {
				width: 100%; }
			.nav-link {
				border-top-left-radius: 20px;
				border-top-right-radius: 20px;
				padding: 0;
				@include fs(16rem);
				border: 0;
				padding: 10px 20px;
				background: darken($white, 10%);
				text-transform: uppercase;
				color: darken($white,40%);
				@include media-breakpoint-down(md) {
					@include bor-rad(0);
					@include fs(14rem); }
				span {
					font-weight: 500; }
				&.active {
					color: $white;
					font-weight: bold;
					background: $blue;
					@include media-breakpoint-down(md) {
						background: none;
						color: $blue; } } } } }
	.tab-content {
		padding: 30px 0px;
		border-bottom: 1px solid darken($white,10%);
		@include media-breakpoint-down(sm) {
			padding: 10px 0; }
		strong {
			display: block;
			margin-bottom: 15px; }
		ul {
			li {
				margin-bottom: 5px;
				&:last-child {
					margin: 0; }
				strong {
					display: block; }
				&:before {
					width: 7px;
					height: 7px;
					background: #d9d9d9; } } }
		.listawards {
			ul {
				padding: 0;
				li {
					&:before {
						display: none; }
					display: inline-block;
					@include media-breakpoint-down(xs) {
						width: 33.3%;
						float: left; }
					img {} } } } } }
.product-detail-page {
	.full-width {
		width: 100%;
		padding: 0; } }
.btn-shownav {
	background: $blue;
	padding: 10px;
	display: none;
	color: $white;
	font-weight: 600;
	&:before {
		content: '\f0c9';
		font-family: 'fontawesome';
		padding-right: 10px; }
	@include media-breakpoint-down(md) {
		display: block; } }
